<template>
  <CHeader position="sticky" class="bg-primary mb-4">
    <CContainer fluid>
      <CHeaderToggler
        :class="[
          'px-md-0',
          'me-md-3',
          'd-lg-none',
          { 'prevent-hide': !sidebarVisible },
        ]"
        @click="$store.commit('toggleSidebar')"
      >
        <CIcon icon="cil-menu" size="lg" />
      </CHeaderToggler>
      <CHeaderBrand class="mx-auto d-md-none text-white" to="/">
        <CIcon :icon="logoNegative" height="34" alt="Logo" />
      </CHeaderBrand>
      <CForm class="d-none d-md-flex">
        <CInputGroup>
          <CInputGroupText
            id="search-addon"
            class="bg-body-secondary border-0 px-1"
          >
            <CIcon
              icon="cil-search"
              size="lg"
              class="my-1 mx-2 text-disabled"
            />
          </CInputGroupText>
          <CFormInput
            placeholder="Search..."
            ariaLabel="Search"
            ariaDescribedby="search-addon"
            class="bg-body-secondary border-0"
          />
        </CInputGroup>
      </CForm>
      <CHeaderNav class="d-none d-sm-flex ms-auto me-3">
        <AppHeaderDropdownNotif />
        <AppHeaderDropdownTasks />
        <AppHeaderDropdownMssgs />
      </CHeaderNav>
      <CHeaderNav class="ms-auto ms-sm-0 me-sm-4">
        <li class="nav-item py-2 py-lg-1">
          <div
            class="vr d-none d-lg-flex h-100 mx-lg-2 text-white text-opacity-50"
          ></div>
          <hr class="d-lg-none my-2 text-white-50" />
        </li>
        <CDropdown variant="nav-item" placement="bottom-end">
          <CDropdownToggle :caret="false">
            <CIcon v-if="getColorMode() === 'dark'" icon="cil-moon" size="xl" />
            <CIcon
              v-else-if="getColorMode() === 'light'"
              icon="cil-sun"
              size="xl"
            />
            <CIcon v-else icon="cil-contrast" size="xl" />
          </CDropdownToggle>
          <CDropdownMenu>
            <CDropdownItem
              :active="getColorMode() === 'light'"
              class="d-flex align-items-center"
              component="button"
              type="button"
              @click="setColorMode('light')"
            >
              <CIcon class="me-2" icon="cil-sun" size="lg" /> Light
            </CDropdownItem>
            <CDropdownItem
              :active="getColorMode() === 'dark'"
              class="d-flex align-items-center"
              component="button"
              type="button"
              @click="setColorMode('dark')"
            >
              <CIcon class="me-2" icon="cil-moon" size="lg" /> Dark
            </CDropdownItem>
            <CDropdownItem
              :active="getColorMode() === 'auto'"
              class="d-flex align-items-center"
              component="button"
              type="button"
              @click="setColorMode('auto')"
            >
              <CIcon class="me-2" icon="cil-contrast" size="lg" /> Auto
            </CDropdownItem>
          </CDropdownMenu>
        </CDropdown>
        <li class="nav-item py-2 py-lg-1">
          <div
            class="vr d-none d-lg-flex h-100 mx-lg-2 text-white text-opacity-50"
